.chatPage {
   display: flex;
   flex-direction: column;
}


.messages {
   overflow-y: auto;
   height: 70vh;
   width: calc(100% + 8px);
   margin-left: 4px;
}

/*Message*/
.message {
   display: flex;
   flex-direction: row;
   padding: 0 12px 6px;
}

.avatar {
   display: flex;
   align-items: flex-end;
}

.avatar img {
   height: 40px;
   width: 40px;
   border-radius: 50%;
   margin-bottom: 20px;
}

.body {
   height: 100%;
   background-color: #efefef;
   display: flex;
   flex-direction: column;
   margin: 0 23px 20px 10px;
   border-radius: 10px 10px 10px 0;
}

.userName {
   padding: 6px 13px 0;
   font-size: 13px;
   font-weight: 500;
   color: #61dafb;
}

.blueButton {
   padding: 10px 20px;
   color: #fff;
   background-color: #61DAFB;
   border: 1px solid #61DAFB;
   border-radius: 20px;
   cursor: pointer;
}

.blueButton:disabled {
   color: #bebebe;
   background-color: #3D92A6;
   border: 1px solid #45A4BBFF;
}
